@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.project-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: min(2vw, 1.8em);
  -webkit-column-gap: min(2vw, 1.8em);
          column-gap: min(2vw, 1.8em);
  padding-bottom: 3em;
}

h3 {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: min(3.2vw, 1.5em);
  color: grey;
}

.project-description {
  font-family: 'Roboto', sans-serif;
  color: grey;
  padding-top: min(3vw, 2.5em);
  font-size: min(2.5vw, 0.8em);
  text-align: center;
}

.text-right {
  grid-column: span 2;
  padding-right: 11vw;
}

.text-left {
  grid-column: 2 / span 2;
  padding-left: 11vw;
}

.badge-container-left {
  grid-column: 2;
  text-align: -webkit-right;
  padding-bottom: 1.5em;
}

.badge-container-right {
  grid-column: 4;
  text-align: -webkit-left;
  padding-bottom: 1.5em;
}

.badge {
  height: min(30vw, 12em);
  width: min(30vw, 12em);
  overflow: hidden;
  border-radius: 50%;
  box-shadow: 2px 5px 8px lightgrey;
}

.badge:hover {
  box-shadow: 2px 5px 8px grey;
}

.badge img {
  height: 100%;
}

.ascii-badge {
  height: min(30vw, 12em);
  width: min(30vw, 12em);
  overflow: hidden;
  border-radius: 50%;
  box-shadow: 2px 5px 8px lightgrey;
}

.ascii-badge img {
  margin-left: -30vw;
  margin-top: -20vh;
  height: 200%
}

.ascii-badge:hover {
  box-shadow: 2px 5px 8px grey;
}

body {
  overflow-x:hidden;
}

h1 {
  display: inline-block;
  font-family: 'Commissioner', sans-serif;
  font-weight: 200;
  font-size: 3em;
  word-spacing: 0.3em;
  text-transform: uppercase;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.4em;
  width: 100vw;
}

h2 {
  font-family: 'Commissioner', sans-serif;
  font-weight: 200;
  font-size: min(6vw, 2em);
  word-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
  padding-top: 2em;
}

ul {
  display: inline-block;
  padding: 0;
  list-style-type: none;
  text-align: center;
  width: 100vw;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1.6em;
}

li {
  font-family: 'Roboto', sans-serif;
  font-size: min(3vw, 0.9em);
  text-transform: uppercase;
  display: inline;
}

li:last-child:after {
  content: "";
  padding: 0.8em;
}

li:after {
  content: "\00B7";
  padding: 1.2em;
}

a:link, a:visited {
  color: black;
  text-decoration: none;
}

a:hover {
  color: grey;
  text-decoration: none;
}

a.active {
  color: chocolate;
  text-decoration: none;
}

.divider
{
  content: "";
  top: 0;
  margin-left: auto;
  margin-right: auto;
  width: 40vw;
  height: 1px;
  background-image: linear-gradient(to right, transparent, chocolate, transparent);
  margin-bottom: 3em;
}

